* {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

html {
    box-sizing: border-box;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

ul {
    list-style: none;
}

body {
    background-color: #d1d6db;
}

.profile {
    width: 250px;
    margin: 30px 25px;
    background-color: #fff;
    /* box-shadow: 10px 5px 5px rgba(0, 0, 0, 0.6); */
    /* box-shadow: 0 8px 6px -6px black; */
    box-shadow:
     0 2.8px 2.2px rgba(0, 0, 0, 0.034),
     0 6.7px 5.3px rgba(0, 0, 0, 0.048),
     0 12.5px 10px rgba(0, 0, 0, 0.06),
     0 22.3px 17.9px rgba(0, 0, 0, 0.072),
     0 41.8px 33.4px rgba(0, 0, 0, 0.086),
     0 100px 80px rgba(0, 0, 0, 0.12);
     border-radius: 15px;
}

.description {
    padding: 20px 0 0;
}

.avatar {
    width: 150px;
    border-radius: 70px;
    margin: 0 auto;
}

.name {
    text-align: center;
    font-size: 22px;
    font-weight: 700;
    margin: 0 auto;
    padding-top: 10px;
}

.tag, .location {
    text-align: center;
    margin: 0 auto;
    font-size: 18px;
    margin: 8px;
}

.stats {
  display: flex;
  justify-content: space-between;
  border: 1px solid rgb(228, 233, 240);
  background-color: rgb(160, 207, 255);
}

.item {
    width: 84px;
    height: 60px;
    text-align: center;
    border: 1px solid blueviolet;
}

.label {
    font-size: 20px;
    font-weight: 300;
    color: indigo;
}

.quantity {
    display: flex;
    justify-content: center;
    padding: 10px;
    font-size: 18px;
    font-weight: 900;
}